.order-page{
  &__status{
    display: flex;
    margin-top: 1.75em;
  }
  &__bottom{
    margin-top: 5em;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    &-col{
      width: 31.25em;
      @include grid100gap(1.75em);
    }
  }
}

.order-table{
  margin-top: 1.75em;
  @media screen and (min-width: 993px){
    th,td{
      width: 14.375em;
      text-align: center;
      &:nth-child(1){
        width: 17.375em;
        text-align: left;
        padding-right: 0;
      }
      &:last-child{
        text-align: left;
      }
    }
  }
}

.order-row2{
  .catalog-info__prices{
    margin: 0;
  }
  &__info{
    display: flex;
    padding: 1em 0;
  }
  &__img{
    width: 5.8125em;
    height: 5.8125em;
    flex-shrink: 0;
    margin-right: .5em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__props{
    color: $text-base-dn;
    padding-top: .5em;
    margin-top: auto;
    @include grid100gap(.25em)
  }
}

.order-summary{
  border: $borderUp;
  padding: 1.75em;
  width: 24.625em;
  &__items,
  &__bottom{
    margin-top: 1em;
  }
  &__bottom,
  &__item{
    display: flex;
    justify-content: space-between;
  }
}

.order-delivery,
.order-pay{
  @include grid100gap(1em);
}

.order-delivery{

}


@media screen and (max-width: 992px){
  .order-page{
    &__bottom{
      margin-top: .5em;
      @include grid100gap(.5em);
      &-col{
        width: 100%;
        gap: .5em;
      }
    }
  }

  .order-row2{
    position: relative;
    padding-left: 7.5em !important;
    &__img{
      position: absolute;
      top: 1em;
      left: 1em;
    }
    &__info{
      padding: 0;
    }
    &__props{
      padding-top: 3.5em;
    }
    &__count{
      &:before{
        content: ': ';
      }
    }
    td{
      width: 100%;
      &:nth-child(2){
        margin-top: .25em;
      }
      &:nth-child(3),
      &:nth-child(4){
        display: none;
      }
      &:nth-child(5){
        position: absolute;
        top: 3.5em;
        left: 7.5em;
        width: calc(100% - 8.5em);
      }
    }
  }

  .order-summary{
    padding: 1em;
    width: 100%;
  }

  .order-delivery,
  .order-pay{
    border: $borderUp;
    padding: 1em;
  }
}


