.air-datepicker{
  width: 18.75em;
  font-size: 1em;
  padding: 1em;
  box-shadow: $dr-shadow !important;
  border: none;
  border-radius: 0;
  font-family: $font1;
  &:before,
  &:after{
    display: none;
  }
}

.air-datepicker--pointer{
  display: none;
}

.air-datepicker-nav{
  border: none;
  padding: 0;
}

.air-datepicker-nav--title,
.air-datepicker-nav--action{
  border-radius: 0;
}

.air-datepicker-nav--title{
  font-size: 1.125em;
  font-weight: 500;
}

.air-datepicker--content{
  padding: 0;
}

.air-datepicker-body--day-names{
  margin: 1em 0 .5em;
}

.air-datepicker-body--day-name{
  font-size: 1.125em;
  line-height: 133%;
  color: $text-on-dark-base-dn;
}

.air-datepicker-body--cells.-days-{
  grid-auto-rows: unset;
}

.air-datepicker-cell{
  height: 2.14em;
  font-size: .875em;
  border-radius: 0;
}

.air-datepicker-cell.-selected-{
  border-radius: 0;
}

.air-datepicker-cell.-day-{
  width: 100%;
  max-width: 2.14em;
  margin: 0 auto;
}

.user{
  display: flex;
  align-items: center;
  &__field{
    flex-shrink: 0;
    position: relative;
    margin-right: 1.125em;
  }
  .img-field{
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__info{
    @include grid100gap(.25em);
  }
}

.img-field{
  cursor: pointer;
  width: 1.75em;
  height: 1.75em;
  @include center;
  border-radius: 50%;
  background-color: $icon-primary-dn;
}


@media screen and (max-width: 992px){
  .user{
    margin-top: 1.75em;
  }
}


@media screen and (max-width: 576px){
  .user{
    padding: 1em;
    border: $borderUp;
    flex-direction: column;
    margin: 1.75em 0 3em;
    &__field{
      margin: 0 0 1.75em;
    }
  }
}
