.m-section2{
  padding: 4.375em 0;
  &--last{
    padding-bottom: 8.75em;
  }
}

.sport-type{
  margin-top: 4.375em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .1875em));
    gap: .375em;
    margin-top: 3em;
  }
}

.sport-card{
  padding: 1.75em;
  position: relative;
  min-height: 17.625em;
  display: block;
  overflow: hidden;
  &:hover{
    color: $black;
    .sport-card__img img{
      transform: scale(1.05);
    }
  }
  &._white{
    color: $text-on-dark-base-m;
    &:hover{
      color: $text-on-dark-base-m;
    }
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      transition: all .6s;
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    flex-direction: column;
    max-width: 20em;
  }
  &__desc{
    margin-top: 1em;
    p{
      @include lastM(0 0 .25em);
    }
  }
  &__license{
    padding-top: 1em;
    margin-top: auto;
    display: flex;
    align-items: center;
    img{
      width: 2em;
      margin-right: .5em;
    }
  }
}

.brands{
  &__groups{
    margin-top: 3em;
    @include grid100gap(5em);
  }
  .brand{
    margin: 0;
    &__img{
      left: 22em;
      width: auto;
      height: 100%;
      img{
        height: 100%;
        width: auto;
      }
      ._view2{
        display: none;
      }
    }
  }
  .collection{
    padding: 0;
    margin-top: .5em;
  }
  .collection-slider{
    margin-top: 0;
  }
}

.categories{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
    gap: .5em;
    margin-top: 3em;
  }
}

.category-card{
  position: relative;
  display: block;
  &._white{
    color: $text-on-dark-base-m;
    &:hover{
      color: $text-on-dark-base-m;
    }
  }
  &._big{
    @media screen and (min-width: 577px){
      grid-column: span 2;
    }
  }
  &__img{
    @include imgCover(95%);
    height: 21em;
    padding-bottom: 0;
    ._view2{
      display: none;
    }
  }
  &__content{
    position: absolute;
    padding: 1.75em;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 2;
  }
}


@media screen and (max-width: 992px){
  .sport-type{
    &__grid{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }

  .brands{
    .brand{
      font-size: .75em;
    }
  }

  .categories{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
    }
  }

  .category-card{
    &__img{
      height: 17.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .sport-card{
    padding: 0;
    color: $text-on-dark-base-m;
    &:hover{
      color: $text-on-dark-base-m;
    }
    &__content{
      padding: 1.75em;
      background-color: rgba(#000,.8);
    }
    &__img{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
  }

  .brands{
    .brand{
      font-size: 1em;
      &__img{
        width: 100%;
        height: auto;
        left: 0;
        img{
          width: 100%;
          height: auto;
          margin: 0;
        }
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }

  .categories{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
