.auth{
  padding: 1.75em 0 8.75em;
  &__inner{
    width: 37.75em;
    margin: 5em auto 0;
    max-width: 100%;
  }
}

.auth-form{
  border: $borderM;
  padding: 3em;
  &__fields{
    margin-top: 1.75em;
    @include grid100gap(1em);
  }
  .m-check,
  .m-captcha{
    margin-top: .75em;
  }
  &__btn{
    width: 100%;
    margin-top: 3em;
  }
  &__buttons{
    margin-top: 3em;
    @include grid100gap(1em);
    .auth-form__btn{
      margin: 0;
    }
  }
  &__bottom{
    margin-top: 1.75em;
    @include center;
  }
  &__link{
    margin-left: 1.125em;
    font-weight: 500;
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link2{
    font-weight: 500;
    color: $text-primary-up;
  }
  &__desc{
    margin-top: 1.75em;
  }
}

.m-captcha{
  display: flex;
  &>img{
    max-width: 21.875em;
    width: 100%;
    display: block;
  }
}


@media screen and (max-width: 992px){
  .auth{
    &__inner{
      margin: 0;
      width: 100%;
    }
  }

  .auth-form{
    padding: 0;
    border: none;
  }
}


@media screen and (max-width: 576px){
  .auth-form{
    &__bottom{
      flex-direction: column;
    }
    &__link{
      margin: .75em 0 0;
    }
    &__wrap{
      flex-direction: column;
      align-items: flex-start;
    }
    &__link2{
      margin-top: 1.625em;
    }
  }
}
