.catalog-menu{
  border: $borderM;
  position: sticky;
  top: 1.5em;
  &__top{
    margin-bottom: .5em;
  }
  &__top,
  &__link{
    padding: 0 .75em;
    display: flex;
    align-items: center;
    height: 2.5em;
    svg{
      width: 1.25em;
      margin-left: .25em;
    }
  }
  &__link{
    &._active{
      background-color: $back-primary-up;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-menu{
    height: calc(100 * var(--vh) - 7.625em);
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    top: unset;
    background-color: #fff;
    z-index: 20;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    overflow-y: auto;
    border: none;
    &__top{
      display: none;
    }
    &__link{
      height: 3.5em;
    }
  }

  ._open-catalog-menu{
    .catalog-menu{
      opacity: 1;
      pointer-events: unset;
    }
    ._toggle-catalog-menu{
      color: $text-primary-m;
      path{
        stroke: $text-primary-m;
      }
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}


