.filter-item__show,
.filter-dates__mob{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 2.5em;
  border: $border;
  padding: 0 .5em;
  transition: all .3s;
  &:after{
    content: '';
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    background-image: url(../img/arrow-down.svg);
    @include bgImgSize(100%);
    margin-left: .25em;
    transition: inherit;
  }
  &:hover{
    cursor: pointer;
    border-color: $icon-primary-m;
    background-color: $text-on-dark-primary-dn;
  }
}

.filter-item{
  position: relative;
  &._open{
    .filter-item__show{
      border-color: $icon-primary-m;
      background-color: $text-on-dark-primary-dn;
      &:after{
        transform: rotate(-180deg);
      }
    }
    .filter-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._show-count{
    .filter-item__count{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{

  }
  &__count{
    padding: 0 .25em;
    height: 1.25em;
    @include center;
    min-width: 1.5em;
    background-color: $icon-primary-dn;
    margin-left: auto;
    color: $text-on-dark-base-m;
    opacity: 0;
    pointer-events: none;
  }
  &__hidden{
    z-index: 5;
    position: absolute;
    margin-top: 1em;
    min-width: 100%;
    box-shadow: $dr-shadow;
    background-color: $bg-light;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__scroll{
    overflow-y: auto;
    max-height: 17em;
    @include scroll;
  }
  &__title{
    margin-bottom: -.25em;
    padding: 1.25em 1.25em 0;
  }
  &__options{
    padding: 1.25em;
    @include grid100gap(1em);
  }
  &__nav{
    padding: .75em 1.25em;
    border-top: 1px solid $line-base-up;
  }
  &__btn{
    width: 100%;
  }
}

.filter-check{
  label{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 2.5em;
    width: 100%;
    border: $border;
    padding: 0 .5em;
    transition: all .3s;
  }
  input:checked{
    &~label{
      border-color: $line-primary-m;
      background-color: $back-primary-up;
    }
  }
}

.filter-option{
  display: flex;
  align-items: center;
  min-height: 1.5em;
  &__icon{
    border: 1px solid $line-base-dn;
    width: 1.25em;
    height: 1.25em;
    flex-shrink: 0;
    margin-right: .5em;
    background-image: url(../img/check.svg);
    @include bgImgSize(0);
  }
  &__count{
    color: $text-base-up;
    margin-left: auto;
  }
  &__img{
    width: 1.25em;
    margin-left: .25em;
  }
  input:checked{
    &~.filter-option__icon{
      background-size: .875em auto;
      border-color: $icon-primary-dn;
      background-color: $back-primary-dn;
    }
  }
}

.filter-option2{
  .filter-option__icon{
    margin: 0 0 0 auto;
    border-radius: 50%;
    background-image: url(../img/radio.svg);
  }
  input:checked{
    &~.filter-option__icon{
      background-size: .75em auto;
      background-color: transparent;
    }
  }
}

.props{
  margin-top: 3em;
  @include grid100gap(1em);
}

.prop-item{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .25em));
  gap: .5em;
  color: $text-base-up;
  &__desc{
    color: $text-base-dn;
  }
}

.catalog-delivery{
  @include grid100gap(.5em);
  margin-top: 3em;
  padding-left: .75em;
  &__item{
    max-width: 22.5em;
    display: flex;
    align-items: flex-start;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }
}


@media screen and (min-width: 993px){
  ._hide-desktop{
    display: none;
  }
}


@media screen and (max-width: 992px){
  ._hide-tablet{
    display: none;
  }

  .catalog-delivery{
    padding-left: 0;
    &__item{
      max-width: 100%;
    }
  }
}


