.brand{
  background: #c3d6de;
  margin: 3.25em 0 2.5em;
  overflow: hidden;
  display: block;
  &:hover{
    color: inherit;
  }
  &._orange{
    background: $back-second-up;
    .brand__inner:before{
      background: rgba(221, 161, 54, 0.28);
    }
  }
  &._blue{
    background: #d6dbe7;
    .brand__inner:before{
      background: rgba(105, 164, 207, 0.28);
    }
  }
  &__inner{
    padding: 4.25em 0;
    min-height: 28em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 37.875em;
      height: 37.875em;
      border-radius: 50%;
      background: rgba(71, 91, 99, 0.6);
      filter: blur(500px);
      top: -5.125em;
      left: 23em;
    }
  }
  &__content{
    max-width: 27.5em;
    position: relative;
    z-index: 2;
  }
  .text{
    margin-top: 1.75em;
  }
  &__img{
    position: absolute;
    left: 18.75em;
    bottom: 0;
    width: 39em;
    z-index: 3;
    img{
      width: 100%;
      display: block;
    }
  }
}

.m-section{
  padding: 2.5em 0;
}

.collection{
  //overflow: hidden;
  position: relative;
  &:hover{
    z-index: 3;
  }
}

.slider{
  .swiper{
    &-container{
      width: calc(100% + .5em);
    }
    &-slide{
      padding-right: .5em;
    }
  }
}

.collection-slider{
  margin-top: 3em;
  position: relative;
  &:hover{
    .swiper-button{
      opacity: 1;
    }
  }
  .slider__wrap{
    //overflow: hidden;
  }
  &__container{
    width: calc(100% + .5em);
  }
  &__slide{
    padding-right: .5em;
    width: 25%;
    @media screen and (min-width: 993px){
      opacity: 0;
      pointer-events: none;
      &.swiper-slide-active,
      &.swiper-slide-next,
      &.swiper-slide-next+.swiper-slide,
      &.swiper-slide-next+.swiper-slide+.swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  .swiper{
    &-button{
      top: 10.375em;
      opacity: 0;
    }
  }
}

.swiper-button{
  width: 2.75em;
  height: 2.75em;
  padding: 0;
  @include center;
  top: 50%;
  transform: translateY(-50%);
  margin: 0;
  &-prev{
    left: 0;
  }
  &-next{
    right: 0;
  }
  svg{
    width: 1.25em;
  }
  &:hover{
    path{
      stroke: $text-base-m;
    }
  }
}

.product-card{
  position: relative;
  &__badges{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .m-badge{
    margin-bottom: .25em;
  }
  &__img{
    background-color: $root-base-up;
    @include imgCover(113%);
  }
  &__content{
    margin-top: 1.25em;
  }
  .catalog-info__prices{
    margin: 0;
  }
  &__info{
    margin-top: .5em;
    @include grid100gap(.25em);
  }
  &__title{
    @include truncate(2);
    min-height: 2.66em;
  }
  &__inner{
    position: relative;
    z-index: 2;
  }
  &__bottom{
    opacity: 0;
    transition: all .3s;
    pointer-events: none;
    position: absolute;
    left: -.5em;
    top: -.5em;
    width: calc(100% + 1em);
    padding: 29.125em .5em .5em;
    box-shadow: 0 0 1.25em 0 rgba(81, 108, 187, 0.32);
    background-color: $bg-light;
  }
  &__sizes,
  &__colors{
    margin-top: 1.25em;
    &-items{
      display: flex;
      flex-wrap: wrap;
    }
  }
  .color-field{
    margin: .25em .5em 0 0;
  }
  .size-field{
    margin: .25em .75em 0 0;
  }
  .favourite-btn{
    position: absolute;
    top: .25em;
    right: .25em;
    z-index: 4;
    opacity: 0;
  }
  &__btn{
    width: 100%;
    margin-top: 1.25em;
  }
  .slider{
    display: block;
    position: relative;
    z-index: 2;
    &__wrap{
      overflow: hidden;
    }
    .swiper-pagination{
      transition: all .3s;
      opacity: 0;
      width: 100%;
      height: 100%;
      padding: .75em;
      margin: 0;
      position: absolute;
      bottom: 0;
      left: 0;
      &-bullet{
        flex-grow: 1;
        margin: 0 .125em !important;
        height: 100%;
        border: none;
        background: transparent;
        border-bottom: .25em solid $back-base-dn;
        &-active{
          border-color: $icon-primary-dn;
        }
      }
    }
  }
  &:hover{
    @media screen and (min-width: 993px){
      z-index: 5;
      .product-card__bottom{
        opacity: 1;
        pointer-events: unset;
      }
      .favourite-btn,
      .swiper-pagination{
        opacity: 1;
      }
    }
  }
}

.color-field{
  input:checked{
    &~label svg{
      opacity: 1;
    }
  }
  label{
    @include center;
    width: 1.5em;
    height: 1.5em;
    svg{
      width: 1.125em;
      opacity: 0;
    }
  }
}

.size-field{
  input:checked{
    &~label{
      font-weight: 500;
      color: $text-primary-up;
    }
  }
  input:disabled{
    &~label{
      color: $text-base-up;
    }
  }
}

.m-badge{
  background-color: $text-error-m;
  height: 2em;
  display: flex;
  align-items: center;
  padding: 0 .75em;
  color: $text-on-dark-base-m;
  &._blue{
    background-color: $icon-primary-dn;
  }
  &._blue2{
    background-color: $text-on-dark-primary-m;
    color: $text-base-m;
  }
}


@media screen and (max-width: 992px){
  .brand{
    &__inner{
      &:before{
        display: none;
      }
    }
  }

  .collection{
    overflow: hidden;
    .h2{
      text-align: center;
    }
  }

  .collection-slider{
    margin-top: 3em;
    .slider__wrap{
      overflow: unset;
      width: 18.375em;
      margin: 0 auto;
    }
    &__slide{
      width: 100%;
    }
  }

  .product-card{
    .swiper-pagination{
      display: none;
    }
    .favourite-btn{
      opacity: 1;
    }
  }
}


@media screen and (max-width: 576px){
  .brand{
    position: relative;
    &__inner{
      padding: 3em 0 24.25em;
      position: static;
    }
    &__img{
      left: 0;
      width: 100%;
      height: 22.5em;
      img{
        width: 185%;
        margin-left: -25%;
      }
    }
  }
}
