.help-group{
  margin-top: 3em;
}

.qa-card{
  border-top: $borderUp;
  &._active{
    .qa-card__btn{
      transform: rotate(-180deg);
    }
  }
  &__show{
    cursor: pointer;
    padding: .5em 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__btn{
    width: 1.25em;
    @include center;
    margin: .25em 0 0;
    img{
      width: 100%;
    }
  }
  &__hidden{
    padding-bottom: 1em;
    max-width: 58.5em;
    display: none;
  }
  .text{
    margin-top: -.25em;
  }
}

.feedback{
  margin-top: 5.5em;
  padding: 3em;
  border: $borderUp;
  &__grid{
    display: grid;
    grid-template-columns: 31.75em calc(100% - 34.75em);
    gap: 3em;
  }
  &__img{
    width: 100%;
    min-height: 100%;
    object-fit: cover;
    display: block;
  }
  &__info{
    @include grid100gap(.5em);
    text-align: center;
  }
}

.feedback-form{
  @include grid100gap(1em);
  .m-captcha,
  .m-check{
    margin-top: .75em;
  }
  &__btn{
    margin-top: 2em;
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .feedback{
    padding: 1em 1.75em;
    width: calc(100% + 3.5em);
    margin-left: -1.75em;
    border-right: none;
    border-left: none;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: 3em .5em;
    }
    &__info{
      padding: 0 1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .help{
    padding-bottom: 0;
  }

  .feedback{
    margin-top: 8.75em;
    border: none;
    padding: 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__info{
      text-align: left;
    }
  }

  .feedback-form{
    padding: 0 1.75em;
  }
}
