.catalog{
  padding: 2.25em 0 8.75em;
  &__grid{
    margin-top: .75em;
    display: grid;
    grid-template-columns: 10.625em calc(100% - 10.625em - 1.75em);
    gap: 0 1.75em;
    position: relative;
    &._full{
      grid-template-columns: 100%;
    }
  }
  &__col{
    padding-top: 1.5em;
  }
  &._big-card-view{
    .catalog-items{
      @media screen and (min-width: 993px){
        grid-template-columns: repeat(2,calc(50% - .25em));
      }
    }
  }
}

.catalog-items{
  row-gap: 3em;
  .product-card{
    &__img{
      padding-bottom: 0;
      height: 20.75em;
      margin-bottom: .5em;
    }
  }
}

.catalog-view{
  position: absolute;
  right: 0;
  top: 0;
  z-index: 4;
  display: flex;
  margin-bottom: .5em;
  &__btn{
    height: 1.25em;
    display: flex;
    align-items: center;
    margin-left: .875em;
    &-num{

    }
    input:checked~label,
    input:hover~label{
      color: $text-primary-up;
      &:after{
        background-color: $icon-primary-m;
      }
    }
    label{
      color: $text-base-up;
      display: flex;
      align-items: center;
      &:after{
        content: '';
        width: .5em;
        height: .625em;
        flex-shrink: 0;
        background-color: $icon-base-up;
        margin-left: .125em;
      }
    }
  }
}

.filter{
  padding-top: 1.5em;
  padding-bottom: 1.75em;
  background-color: #fff;
  position: sticky;
  top: 0;
  z-index: 3;
  &__top,
  &__bottom{
    display: none;
  }
}

.filter-nav,
.catalog-top{
  display: none;
}


@media screen and (max-width: 992px){
  .catalog{
    padding: 0 0 3em;
    &__grid{
      margin-top: 3em;
      grid-template-columns: 100%;
    }
    &__col{
      padding: 0;
    }
  }

  .catalog-view{
    display: none;
  }

  .catalog-top{
    margin-bottom: 1.25em;
    display: block;
    border-bottom: 1px solid $line-base-m;
    .container{
      padding: 0 .75em;
    }
    &__inner{
      height: 3em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &._center{
        .catalog-top__btn{
          width: 100%;
          justify-content: center;
        }
      }
    }
    &__btn{
      display: flex;
      align-items: center;
      svg{
        width: 1.25em;
        margin-left: .25em;
      }
    }
  }

  .filter{
    height: calc(100 * var(--vh) - 4.5625em);
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    top: unset;
    background-color: #fff;
    z-index: 20;
    margin: 0;
    padding: 1.5em 1em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    overflow-y: auto;
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 1.5em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background-color: rgba($bg-dark,.8);
      z-index: 19;
      display: none;
    }
    &__close{
      width: 1.25em;
      @include center;
      img{
        width: 100%;
      }
    }
    .filter-check{
      &:not(:first-child){
        label{
          border-top: none;
        }
      }
      input:checked{
        &~label{
          border-color: $line-base-up;
          background-color: $back-primary-up;
        }
      }
      label{
        height: 3em;
      }
    }
    .filter-item{
      &:not(:first-child){
        .filter-item__show{
          border-top: none;
        }
      }
      &._show-count{
        .filter-item__show{
          background-color: $back-primary-up;
        }
      }
      &__show{
        height: 3em;
        justify-content: unset;
        &:after{
          margin-left: auto;
          transition: unset;
        }
      }
      &__count{
        margin-left: .5em;
      }
      &__hidden{
        position: fixed;
        bottom: 0;
        left: 0;
        top: unset;
        margin: 0;
        width: 100%;
        height: calc(100 * var(--vh) - 4.5625em);
        //opacity: 1;
        //pointer-events: unset;
      }
      &__nav{
        display: none;
      }
      &__scroll{
        height: calc(100% - 12.25em);
        max-height: unset;
        overflow-y: auto;
      }
    }
    &__bottom{
      z-index: 22;
      box-shadow: .125em .125em .5em rgba(180, 185, 198, 0.6);
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 1em 1em 1.75em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .filter-nav{
    padding: 1.75em 1em;
    display: block;
    &__top{
      padding: .25em 0;
      color: $text-base-up;
    }
    &__bottom{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__close{
      width: 1.25em;
      @include center;
      img{
        width: 100%;
      }
    }
  }

  ._open-filter{
    .filter{
      opacity: 1;
      pointer-events: unset;
    }
  }

  ._open-sub-filter{
    .header{
      z-index: 1;
    }
    .filter{
      overflow: hidden;
      &__bg{
        display: block;
      }
    }
  }
}


