.page-block{
  padding: 1.75em 0 8.75em;
}

.profile{
  &__title{
    margin-top: .25em;
  }
  &__links{
    display: flex;
    margin-top: 1.75em;
  }
  &__link{
    margin-right: 3.5em;
    &:last-child{
      margin-right: 0;
    }
    &:hover{
      color: $text-primary-m;
    }
    &._active{
      color: $text-primary-m;
      font-weight: 500;
    }
  }
  &__grid{
    margin-top: .5em;
  }
}

.user-info,
.user-info2{
  padding: 3em;
  border: $borderUp;
}

.avatar{
  width: 6.25em;
  height: 6.25em;
  flex-shrink: 0;
  border-radius: 50%;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: inherit;
    object-fit: cover;
  }
}

.user-info{
  margin-top: 1.75em;
  display: flex;
  justify-content: space-between;
  &__col{
    display: flex;
    align-items: center;
  }
  &__img{
    margin-right: 1.75em;
  }
  &__text{
    @include grid100gap(.5em);
  }
  &__tags{
    display: flex;
    .m-tag{
      margin: 1em 1em 0 0;
    }
  }
  &__sale{
    @include grid100gap(.3125em);
  }
}

.m-tag{
  padding: 0 1em;
  height: 2.5em;
  display: flex;
  align-items: center;
  background-color: $back-primary-up;
  border-radius: 1.75em;
  color: $icon-base-dn;
  &._red{
    background-color: $back-error-m;
  }
  &._yellow{
    background-color: $back-second-up;
  }
  &._gray{
    background-color: $back-base-up;
  }
  &._blue{
    background-color: $back-primary-m;
  }
  &._orange{
    background-color: $back-second-m;
  }
  &._gray2{
    background-color: $back-base-m;
  }
}

._full{
  grid-column: 1/-1;
}

.user-info2{
  &__items{
    margin-top: 3em;
    @include grid100gap(1em);
  }
  &__item{
    display: grid;
    grid-template-columns: 12.5em calc(100% - 14.25em);
    gap: 1.75em;
  }
}

.profile-nav{
  margin-top: 1.75em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__link{
    @include lastM(0 0 1em);
  }
}


@media screen and (max-width: 992px){
  .user-info,
  .user-info2{
    padding: 1em;
  }

  .user-info2{
    &__items{
      margin-top: 1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .profile{
    &__links{
      width: calc(100% + 1.375em);
    }
    &__link{
      margin-right: 0;
      flex-grow: 1;
      padding: .5em 0 1em;
      @include center;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: .1875em;
        background-color: $text-primary-up;
        transition: all .3s;
        opacity: 0;
      }
      &._active{
        color: $text-primary-up;
        &:before{
          opacity: 1;
        }
      }
    }
  }

  .user-info{
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__col{
      flex-direction: column;
    }
    &__img{
      margin: 0 0 1.75em;
    }
    &__tags{
      justify-content: center;
      .m-tag{
        margin-left: .5em;
        margin-right: .5em;
      }
    }
    &__col2{
      margin-top: 1.75em;
    }
    &__sale{
      display: flex;
      align-items: center;
      gap: unset;
      .text18{
        margin-right: .7em;
      }
    }
  }

  .user-info2{
    text-align: center;
    &__item{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .profile-nav{
    margin-top: 3em;
    align-items: center;
    text-align: center;
    &__link{
      @include lastM(0 0 1.75em);
    }
  }
}
