.orders-filter{
  padding-top: 1.75em;
  margin-top: 1.75em;
  border-top: $borderUp;
  display: flex;
  align-items: flex-end;
  @media screen and (min-width: 993px){
    .filter{
      padding: 0;
      position: static;
      flex-grow: 1;
      margin-left: 1em;
      &__grid{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        gap: unset;
      }
    }
    .filter-item{
      &__hidden{
        left: unset;
        right: 0;
      }
    }
    .filter-option{
      white-space: nowrap;
      &__icon{
        order: -1;
        margin: 0 .5em 0 0;
      }
    }
    &__btn{
      display: none;
    }
  }
  &__search{
    width: 24.125em;
    max-width: 100%;
    input{
      padding-left: 2.375em;
    }
  }
  &__clear{
    margin-top: 1.5em;
    display: flex;
  }
}

.filter-dates{
  &__fields{
    display: flex;
  }
  .fg{
    width: 12.125em;
    margin-right: .5em;
  }
  &__mob{
    display: none;
  }
}

.m-table{
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th,td{
    padding: 0 1em;
    border-top: $borderUp;
    &:first-child{
      border-left: $borderUp;
    }
    &:last-child{
      border-right: $borderUp;
    }
  }
  tbody tr:last-child{
    td{
      border-bottom: $borderUp;
    }
  }
  th{
    height: 3em;
    text-align: left;
    font-weight: 500;
    background-color: $back-primary-m;
    color: $text-base-dn;
  }
  td{
    height: 3.5em;
  }
}

.orders-table{
  margin-top: 3.5em;
  th,td{
    width: 20%;
  }
}

.order-row{
  position: relative;
  transition: all .3s;
  &:hover{
    background-color: $back-primary-up;
  }
  &__link{
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    z-index: 2;
  }
  &__badges{
    display: flex;
  }
}


@media screen and (max-width: 992px){
  .orders-filter{
    padding-top: 0;
    border: none;
    &__btn{
      width: 100%;
      height: 3em;
      @include center;
      border: $borderM;
      img{
        width: 1.25em;
        margin-left: .5em;
      }
    }
    &__mob{
      width: 100%;
      @include grid100gap(1em);
    }
    &__search{
      width: 100%;
    }
    &__clear{
      display: none;
    }
  }

  ._open-date-filter{
    .filter-dates{
      opacity: 1;
      pointer-events: unset;
    }
  }

  .filter-dates{
    position: fixed;
    bottom: 0;
    left: 0;
    top: unset;
    margin: 0;
    width: 100%;
    height: calc(100 * var(--vh) - 4.5625em);
    background-color: #fff;
    z-index: 10;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__fields{
      display: block;
      padding: 0 1em;
    }
    .fg{
      width: 100%;
      margin: 0 0 1.75em;
    }
    &__mob{
      display: flex;
      height: 3em;
    }
  }

  .order-table,
  .orders-table{
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(.5em);
    }
    tr{
      display: flex;
      flex-wrap: wrap;
      padding: 1em;
      border: $borderUp;
    }
    td{
      width: auto;
      display: block;
      border: none !important;
      padding: 0;
      height: auto;
    }
  }

  .orders-table{
    margin-top: 1.75em;
    td{
      &:first-child{
        width: 100%;
        order: 1;
      }
      &:last-child{
        order: 2;
        width: 100%;
        margin: .5em 0;
      }
      &:nth-child(3){
        order: 3;
      }
      &:nth-child(4){
        order: 4;
      }
      &:nth-child(2){
        order: 5;
        margin-left: auto;
      }
    }
  }

  .order-row{
    &__id{
      font-size: 1.5em;
      line-height: 117%;
      font-weight: 500;
      &:before{
        content: ' ';
      }
    }
  }
}


