.about-main{
  padding-top: 1.75em;
}

.history{
  display: grid;
  grid-template-columns: 18.375em calc(100% - 18.875em);
  gap: 1.75em .5em;
  margin-top: 3em;
  &__img{
    width: 100%;
    min-height: 100%;
    object-fit: cover;
    display: block;
  }
}

.principle{
  &__img{
    width: 100%;
    display: block;
    margin-top: .5em;
  }
}

.principle-card{
  padding-top: 5em;
  padding-bottom: 5em;
  background-color: $text-on-dark-base-m;
  @media screen and (min-width: 993px){
    &:nth-child(odd){
      padding-left: calc((100vw - 1280px) / 2 + 2.5em);
      padding-right: 7.5em;
    }
    &:nth-child(even){
      padding-right: calc((100vw - 1280px) / 2 + 2.5em);
      padding-left: 7.5em;
    }
    @media screen and (max-width: 1280px){
      &:nth-child(odd){
        padding-left: 2.5em;
        padding-right: 2.5em;
      }
      &:nth-child(even){
        padding-left: 2.5em;
        padding-right: 2.5em;
      }
    }
  }
  &__text{
    margin-top: 1em;
    @include grid100gap(.5em);
  }
}

.award-card{
  @media screen and (min-width: 577px){
    &:nth-child(even){
      .award-card__img{
        order: -1;
      }
    }
  }
  &__content{
    min-height: 31.375em;
    background-color: $back-base-up;
    padding: 6.375em;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__img{
    img{
      display: block;
      width: 100%;
      min-height: 100%;
      object-fit: cover;
    }
  }
  &__text{
    margin-top: 1em;
    @include grid100gap(1.75em);
  }
}

.team-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        transform: unset !important;
        display: grid;
        gap: 1.75em .5em;
        grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
      }
      &-slide{
        padding-right: 0;
      }
    }
  }
}

.team-card{
  &__img{
    margin-bottom: 1em;
    @include imgCover(134.4%);
  }
}

.legal-data{
  margin-top: 4.375em;
  padding-top: 5em;
  background-color: rgba($root-base-up,.8);
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3.1875em));
    gap: 6.375em;
    align-items: flex-start;
  }
  &__col{
    @include grid100gap(1em);
  }
}


@media screen and (max-width: 992px){
  .history{
    margin-top: 1.75em;
    width: calc(100% + 3.5em);
    margin-left: -1.75em;
    &__col{
      text-align: center;
    }
    &__col,
    &__col2{
      grid-column: 1/-1;
      padding: 0 1.75em;
    }
  }

  .principle-card{
    padding: 1em;
  }

  .award-card{
    &__content{
      padding: 1em;
      min-height: unset;
    }
  }

  .team{
    overflow: hidden;
  }

  .team-slider{
    .swiper-slide{
      width: 18.875em;
    }
  }

  .legal-data{
    padding: 1.75em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .history{
    grid-template-columns: 100%;
    &__col{
      padding: 1.25em 0;
    }
    &__col4{
      order: -1;
    }
  }

  .principle{
    .h2{
      text-align: left;
    }
    &__grid{
      padding: 0 1.75em;
    }
    &__img{
      margin-top: 1em;
    }
  }
}
