.about{
  margin-top: 3em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 5em));
    gap: 3em 10em;
    align-items: flex-start;
  }
  &__group{
    @include grid100gap(.5em);
    .h3{
      font-weight: 400;
    }
  }
}

.req-modal{
  .modal__dialog{
    width: 38.75em;
  }
  .modal-form{
    gap: 1.75em;
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }
  &__inner{
    width: 31.75em;
    max-width: 100%;
    margin: 0 auto;
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


