.pay-ways{
  &__items{
    @include grid100gap(1em);
  }
}

.pay-way{
  display: flex;
  align-items: flex-start;
  &__icon{
    width: 1.75em;
    margin-right: .75em;
  }
}

._mt80{
  margin-top: 5em;
}

._mt16{
  margin-top: 1em;
}

.delivery-way{
  height: 12.5em;
  padding: 1.75em;
  @include center;
  text-align: center;
  border: $borderUp;
  &__img{
    max-width: 100%;
    max-height: 100%;
  }
  &__icon{
    width: 1.75em;
    margin: 0 0 .5em;
  }
  &--type2{
    flex-direction: column;
  }
}

.delivery-calc{
  padding: 3em;
  border: $borderUp;
  display: grid;
  grid-template-columns: calc(100% - 36.75em - 4.375em) 36.75em;
  gap: 3em;
  justify-content: space-between;
  &__info{
    @include grid100gap(1em);
    .h3{
      max-width: 15em;
    }
  }
}

.delivery-form{
  @include grid100gap(1em);
  &__btn{
    margin-top: .75em;
  }
  &__result{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: .5em;
    border-top: $borderUp;
  }
}

.offices{
  &__grid{
    margin-top: 1.75em;
    padding: 1.75em;
    border: $borderUp;
    display: grid;
    grid-template-columns: 31.75em calc(100% - 32.25em);
    gap: .5em;
  }
  &__col{
    max-height: 31.125em;
    overflow-y: auto;
    @include scroll;
  }
  &__items{
    @include grid100gap(1em);
  }
  @media screen and (min-width: 993px){
    .tabs{
      display: none;
    }
    .tab-content{
      display: block;
    }
  }
}

.map{
  height: 31.125em;
}

.office-item{
  font-weight: 500;
  cursor: pointer;
  transition: all .3s;
  &:hover,
  &._active{
    color: $text-primary-up;
  }
}


@media screen and (max-width: 992px){
  .delivery-calc{
    grid-template-columns: 100%;
    padding: 0;
    border: none;
  }

  .offices{
    margin-bottom: 5em;
    &__grid{
      border: none;
      padding: 0;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__col{
      max-height: unset;
    }
    &__items{
      margin-top: 0;
    }
    &__subtitle{
      display: none;
    }
  }
}


