.tabs{
  display: flex;
}

.tab{
  padding: .5em .75em 1em;
  display: flex;
  justify-content: center;
  position: relative;
  font-weight: 500;
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: .1875em;
    left: 0;
    bottom: 0;
    background-color: $text-primary-up;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: 1.125em;
    line-height: 133%;
  }
  &:hover{
    color: $text-primary-up;
    cursor: pointer;
  }
  &._active{
    color: $text-primary-up;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
