._mt48{
  margin-top: 3em;
}

._m28{
  margin-top: 1.75em;
}

.how-card{
  padding: 3em;
  background-color: $root-base-up;
  &._big{
    grid-column: 1/-1;
    .how-card__bottom{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 3em));
      gap: 2.5em 6em;
      align-items: flex-start;
    }
  }
  &__top{
    display: flex;
    align-items: flex-start;
    img{
      width: 1.75em;
      margin-right: .5em;
    }
  }
  .h3{
    font-weight: 400;
  }
  &__bottom{
    margin-top: 1.75em;
  }
  &__group{
    @include grid100gap(1em);
  }
  &__item{
    &-title{
      color: $text-primary-up;
      font-weight: 500;
    }
  }
}

.collection2{
  .h2{
    text-align: center;
    font-size: 2.625em;
  }
}

.condition{
  &__grid{
    display: flex;
    @media screen and (min-width: 993px){
      font-size: calc(1440px * (16 / 1440));
      @media screen and (max-width: 1440px){
        font-size: calc(100vw * (16 / 1440));
      }
    }
  }
}

.condition-card{
  height: 43.75em;
  position: relative;
  transition: all .3s;
  overflow: hidden;
  &._white{
    color: $text-on-dark-base-m;
  }
  @media screen and (min-width: 993px){
    width: calc(50% - .25em);
    &:first-child{
      margin-right: .5em;
    }
    &._disable{
      .condition-card__content,
      .condition-card__content2{
        opacity: 0;
        pointer-events: none;
      }
    }
    &:hover{
      .condition-card__toggle{
        opacity: 1;
      }
    }
    &._active{
      width: calc(100% - 15.5em);
      color: $text-on-dark-base-m;
      flex-shrink: 0;
      .condition-card__inner{
        background-color: rgba(#171717,.6);
      }
      .condition-card__content2{
        opacity: 1;
        pointer-events: unset;
      }
      .condition-card__toggle{
        img{
          transform: rotate(-180deg);
        }
      }
    }
    &__content2{
      pointer-events: none;
      opacity: 0;
      transition: all .3s;
    }
    &__toggle{
      opacity: 0;
    }
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 3em 2.5em;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    z-index: 2;
  }
  &__content,
  &__content2{
    width: 32.5em;
    flex-shrink: 0;
  }
  &__content{
    @include grid100gap(1.75em);
  }
  &__content2{
    padding-top: 6.75em;
  }
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    &-title{
      color: $text-primary-up;
      font-weight: 500;
    }
  }
  &__btn{
    margin-top: 1.75em;
    width: 100%;
  }
  &__toggle{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
    padding: 0 1.875em;
    img{
      width: 1.25em;
      transition: all .3s;
    }
  }
}


@media screen and (max-width: 992px){
  .how-work{
    &__grid{
      width: calc(100% + 3.5em);
      margin-left: -1.75em;
    }
  }

  .how-card{
    padding: 3em 1.75em;
    &._big{
      grid-column: 1/-1;
      .how-card__bottom{
        grid-template-columns: repeat(2,calc(50% - 1.75em));
        gap: 2.5em 3.5em;
      }
    }
  }

  .condition{
    .h1{
      hyphens: auto;
    }
    &__grid{
      @include grid100gap(.5em);
    }
  }

  .condition-card{
    color: $text-on-dark-base-m;
    height: auto;
    &._active{
      .condition-card__toggle{
        img{
          transform: rotate(-90deg);
        }
      }
    }
    &__inner{
      position: relative;
      height: auto;
      background-color: rgba(#171717,.6);
      display: block;
      padding-bottom: 10em;
    }
    &__content,
    &__content2{
      width: 100%;
    }
    &__content2{
      padding-top: 1.75em;
      display: none;
    }
    &__toggle{
      bottom: 0;
      top: unset;
      img{
        transform: rotate(90deg);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .how-card{
    &._big{
      .how-card__bottom{
        grid-template-columns: 100%;
      }
    }
  }

  .condition-card{
    &__btn{
      height: 4em;
    }
  }
}
